import { connect } from 'react-redux';
import KYCSettingsComponent from './KYCSettingsComponent';
import { kycVerificationLevelsSelector } from '../../redux/selectors/kycLevelsSelectors';
import { getUserConfig } from '../../redux/actions/userActions';
import { openKYCSidePane } from '../../redux/actions/sidePaneActions';
import { getKYCLevelComponent } from './KYCTypeSwitchHelper';

const mapStateToProps = state => {
  const {
    verificationLevel,
    levelIncreaseStatus,
    kycConfig
  } = kycVerificationLevelsSelector(state);

  const LevelsComponent = getKYCLevelComponent(kycConfig);

  const highlightStyle = kycConfig.highlightStyle;

  return {
    verificationLevel,
    levelIncreaseStatus,
    kycConfig,
    highlightStyle,
    userId: state.user.userInfo.UserId,
    userName: state.user.userInfo.UserName,
    LevelsComponent
  };
};

const mapDispatchToProps = dispatch => ({
  getUserConfig: (userId, username) =>
    dispatch(getUserConfig(userId, username)),
  openKYCSidePane: () => dispatch(openKYCSidePane())
});

const KYCSettingsContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(KYCSettingsComponent);

export default KYCSettingsContainer;



