import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import get from 'lodash/get';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import { kycConfigLevelShape } from '../../propTypes/kycConfigShapePropTypes';
import './KYCSettingsComponent.css';

const kycClasses = getBEMClasses('kyc-settings');

export default class KYCSettingsComponent extends React.Component {
  static contextTypes = {
    t: PropTypes.func.isRequired
  };

  static propTypes = {
    userId: PropTypes.number,
    userName: PropTypes.string,
    values: PropTypes.object,
    verificationLevel: PropTypes.number,
    levelIncreaseStatus: PropTypes.oneOfType([
      PropTypes.string,
      PropTypes.number
    ]),
    openKYCSidePane: PropTypes.func,
    kycConfig: PropTypes.shape({
      type: PropTypes.string,
      levels: PropTypes.arrayOf(PropTypes.shape(kycConfigLevelShape))
    })
  };

  componentDidUpdate(prevProps) {
    const { userId, userName, getUserConfig } = this.props;

    if (!prevProps.userId && !prevProps.userName && userId && userName) {
      getUserConfig(userId, userName);
    }
  }

  render() {
    const {
      kycConfig,
      verificationLevel,
      levelIncreaseStatus,
      openKYCSidePane,
      LevelsComponent,
      highlightStyle
    } = this.props;
    const levels = get(kycConfig, 'levels', []);
    const level1 = levels[0];
    const level2 = levels[1];
    const level3 = levels[2];
    const level4 = levels[3];

    const KYCWorkflowLevelsComponentProps = {
      highlightStyle,
      verificationLevel,
      levelIncreaseStatus,
      openKYCSidePane,
      levels,
      level1,
      level2,
      level3,
      level4
    };

    return (
      <div className={classnames(kycClasses('', highlightStyle))}>
        <div className={kycClasses('header')}>
          <div className={kycClasses('header-text')}>
            {this.context.t('Current Verification Level')}
          </div>
          <div className={kycClasses('description')}>
            {this.context.t(
              `For your security, some of our services require you to provide basic or additional levels of verification.`
            )}
          </div>
          <div className={kycClasses('description')}>
            {this.context.t(
              `To verify your account or to check what your current verification level is, see below.`
            )}
          </div>
        </div>
        <div className={kycClasses('levels')}>
          <LevelsComponent {...KYCWorkflowLevelsComponentProps} />
        </div>
      </div>
    );
  }
}



